import _get from 'lodash.get';
import { TForm, HeadCell } from '../../../types';

export type NewTForm = TForm & {
	CategoryName?: string;
	FormTypeName?: string;
};

export const headCells: HeadCell<TForm>[] = [
	{
		id: 'Title',
		numeric: false,
		label: 'Title',
		align: 'left',
		disablePadding: false,
	},
	{
		id: 'Category',
		numeric: false,
		label: 'Category',
		align: 'left',
		disablePadding: false,
	},
	{
		id: 'FormType',
		numeric: false,
		label: 'Form Type',
		align: 'left',
	},
	{
		id: 'CreatedAt',
		numeric: false,
		label: 'Created Date',
		align: 'left',
	},
	{
		id: 'UpdatedAt',
		numeric: false,
		label: 'Updated Date',
		align: 'left',
	},
];

const reorderFormTypes = (formTypes = []) =>
	formTypes.reduce((lastType, formType) => {
		const formTypeId = _get(formType, 'id');
		const formTypeName = _get(formType, 'Name', formTypeId);
		return { ...lastType, [formTypeId]: formTypeName };
	}, {});

const reorderCategories = (listFormCategories = []) =>
	listFormCategories.reduce((lastCategories, category) => {
		const categoryId = _get(category, 'id');
		const categoryName = _get(category, 'Title', categoryId);
		return {
			...lastCategories,
			[categoryId]: {
				categoryName,
				formTypes: reorderFormTypes(_get(category, 'FormTypes', [])),
			},
		};
	}, {});

export const mapFormsCategory = (
	forms: Array<TForm>,
	listFormCategories = []
): Array<NewTForm | TForm> => {
	const categories = reorderCategories(listFormCategories);
	return forms.map((form: TForm) => {
		const categoryId = _get(form, 'CategoryId');
		const formTypeId = _get(form, 'FormTypeId');
		return {
			...form,
			CategoryName: _get(categories, [categoryId, 'categoryName']),
			FormTypeName: _get(categories, [categoryId, 'formTypes', formTypeId]),
		};
	});
};
