import React from 'react';
import {
	Checkbox,
	TableCell,
	TableHead,
	TableSortLabel,
	TableRow,
} from '@material-ui/core';
import { makeStyles } from '@material-ui/core/styles';
import { visuallyHidden } from '@material-ui/utils';

import { EnhancedTableHeadProps, HeadCell, TForm } from '../../../types';
import { headCells } from './helpers';
import { ORDER } from '../../../utils/table';

const useStyles = makeStyles(() => ({
	sortSpan: { ...visuallyHidden },
}));

const ListHeader = (props: EnhancedTableHeadProps<TForm>) => {
	const classes = useStyles();
	const {
		onSelectAllClick,
		order,
		orderBy,
		numSelected,
		rowCount,
		onRequestSort = () => {},
	} = props;

	const isSorted = (headCell: HeadCell<TForm>): undefined | boolean =>
		orderBy && orderBy.id === headCell.id;

	return (
		<TableHead>
			<TableRow>
				<TableCell padding='checkbox' sx={{ pl: 3 }}>
					<Checkbox
						color='primary'
						indeterminate={numSelected > 0 && numSelected < rowCount}
						checked={rowCount > 0 && numSelected === rowCount}
						onChange={onSelectAllClick}
						inputProps={{
							'aria-label': 'select all desserts',
						}}
					/>
				</TableCell>
				{headCells.map((headCell: HeadCell<TForm>) => (
					<TableCell
						key={headCell.id}
						align={headCell.numeric ? 'right' : 'left'}
						padding={headCell.disablePadding ? 'none' : 'normal'}
						sortDirection={isSorted(headCell) ? order : undefined}
					>
						<TableSortLabel
							active={isSorted(headCell)}
							direction={isSorted(headCell) ? order : ORDER.ASC}
							onClick={() => onRequestSort(headCell)}
						>
							{headCell.label}
							{isSorted(headCell) ? (
								<span className={classes.sortSpan}>
									{order === ORDER.DESC ? 'sorted descending' : 'sorted ascending'}
								</span>
							) : null}
						</TableSortLabel>
					</TableCell>
				))}
				<TableCell sortDirection={false} align='center' sx={{ pr: 3 }}>
					Action
				</TableCell>
			</TableRow>
		</TableHead>
	);
};

export default ListHeader;
